@import url("https://fonts.googleapis.com/css2?family=Sawarabi+Gothic&display=swap");

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
  background-color: #0f0a25;
  color: white;
  font-size: 1rem;
  font-family: "Sawarabi Gothic", sans-serif;
}

.App {
  width: 70vw;
  margin: 40px auto 0px auto;
}

.randomHiragana {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 4vh 0;
}

.randomHiragana-prompt {
  color: #a3a3a3;
}

.invisible {
  visibility: hidden;
}
#showButton {
  border-radius: 3px;
  opacity: 1;
  margin-top: 15px;
  background-color: grey;
}
#showButton:hover {
  opacity: 0.6;
}

.reset {
  margin-top: 10vh;
  outline: none;
  border-radius: 3px;
  background: none;
  color: white;
}

.ans {
  color: rgb(231, 26, 26);
}

button,
input {
  border: none;
  outline: none;
}
.submit {
  border-radius: 0px 4px 4px 0px;
  background-color: #8f8da3;
  height: 100%;
}

input {
  width: 10vw;
  border-radius: 4px 0px 0px 4px;
  padding-left: 3vw;
  background-color: #ffffff;
  border-right: 1px solid rgb(124, 118, 118);
}

.hidden {
  display: none;
}

.score {
  display: flex;
  flex-direction: column;
}

.score-correct {
  color: rgb(74, 231, 26);
}
.score-incorrect {
  color: rgb(231, 26, 26);
}

table {
  margin: auto;
}
td {
  width: 20%;
}

.option {
  margin-top: 8vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.mode {
  width: 20vw;
  height: 13vh;
  border-radius: 25px;
  margin: 5px;
  background-color: rgb(161, 169, 192);
}

.mode:hover {
  opacity: 0.8;
}

.mode:focus {
  background-color: rgb(215, 225, 255);
}
